import { Fragment, useEffect, useContext } from 'react'
import { Row, Col, CardTitle } from 'reactstrap'
import { useDispatch, useSelector } from 'react-redux'
import GoalOverview from './GoalOverview'
//import Installation from './Installation'
// import SubscribersGained from './Profile'
// import { kFormatter } from '@utils'
//import StatsCard from './StatsCard'
// import Newuser from './Newuser'
import StatsVertical from '@components/widgets/stats/StatsVertical'
import Install from '@components/widgets/stats/Install'
import Uninstall from '@components/widgets/stats/Uninstall'
import { ThemeColors } from '@src/utility/context/ThemeColors'
import { Mail, Smartphone,  TrendingDown, ArrowUpRight } from 'react-feather'
import {
  getUserSignedUpCalendar,
  getUserSignedUpPlatforms
} from '../store/action'

import '@styles/react/libs/charts/apex-charts.scss'
import '@styles/base/pages/dashboard-ecommerce.scss'
import CardBrowserState from './CardBrowserState'

const StatisticsCards = () => {
  const dispatch = useDispatch(),
    store = useSelector((state) => state.dashboard)
     const { colors } = useContext(ThemeColors),
       trackBgColor = '#e9ecef'

  useEffect(() => {
    dispatch(getUserSignedUpCalendar())
    dispatch(getUserSignedUpPlatforms())
  }, [dispatch])

  const { userSignUpCountPlatform } = store
  const { userSignupCalendarCount } = store

  return (
    <Fragment>
    
      <Row>
        <Col  lg="6" xs="6" >
        <CardTitle tag="h4">User Signups</CardTitle>
        <Row className="mt-0"> 
           <Col lg="4" xs="6">
            <StatsVertical
              icon={<ArrowUpRight size={21} />}
              color="success"
              stats={userSignupCalendarCount.today_sinup_count}
              statTitle="Today"
            />
          </Col>
          <Col lg="4" xs="6">
            <StatsVertical
              icon={<TrendingDown size={21} />}
              color="danger"
              stats={userSignupCalendarCount.this_week_sinup_count}
              statTitle="This Week"
            />
          </Col>
          <Col lg="4" xs="6">
            <StatsVertical
              icon={<ArrowUpRight size={21} />}
              color="primary"
              stats={userSignupCalendarCount.this_month_sinup_count}
              statTitle="This Month"
            />
          </Col>
           </Row>
        </Col>
        {/* <Col  lg="8" xs="6" >
        <CardTitle tag="h4">Redeems</CardTitle>
        <Row className="mt-0"> 
        <Col lg='6' sm='6'>
          <SubscribersGained kFormatter={kFormatter} />
        </Col>
          <Col lg="6" xs="6">
          <Newuser kFormatter={kFormatter} warning={colors.warning.main} />
          </Col>
         
           </Row>
        </Col> */}
        <Col  lg="6" xs="6" >
        <CardTitle tag="h4">User by sports</CardTitle>
        <Row className="mt-0"> 
        <Col lg="12" sm="12">
          <CardBrowserState colors={colors} trackBgColor={trackBgColor} />
        </Col>   
        </Row>
        </Col>
      </Row>
      <Row>      
      <Col  lg="4" xs="6" >
        <CardTitle tag="h4">Devices</CardTitle>
        <Row className="mt-0"> 
        <Col lg="12" sm="12">
        <GoalOverview />
        </Col>   
        </Row>
        </Col>
        <Col  lg="4" xs="6" >
        <CardTitle tag="h4">New Signup</CardTitle>
        <Row className="mt-0"> 
        <Col lg="6" xs="6">
            <StatsVertical
              icon={<Smartphone size={21} />}
              color="danger"
              stats={userSignUpCountPlatform.mobile_signup_count}
              statTitle="Signup via Mobile"
              
            />
          </Col>
          <Col lg="6" xs="6">
            <StatsVertical
              icon={<Mail size={21} />}
              color="primary"
              stats={userSignUpCountPlatform.email_signup_count}
              statTitle="Signup via Email"
            />
          </Col>
        </Row>
        </Col>
        <Col  lg="4" xs="6" >
        <CardTitle tag="h4">App Installs</CardTitle>
        <Row className="mt-0"> 
        <Col lg="6" xs="6">
            <Install
              icon={<TrendingDown size={21} />}
              color="white"
              stats={0}
              statTitle="Install"
            />
          </Col>
          <Col lg="6" xs="6">
            <Uninstall
              icon={<ArrowUpRight size={21} />}
              color="danger"
              stats={0}
              statTitle="uninstall"
            />
          </Col>
        </Row>
        </Col>
      
        {/* Stats Card */}
      </Row>
      <Row>
        {/* <Col  lg="8" xs="6" >
        <CardTitle tag="h4">Engagement</CardTitle>
        <Row className="mt-0"> 
        <Col lg='6' sm='6'>
          <SubscribersGained kFormatter={kFormatter} />
        </Col>
          <Col lg="6" xs="6">
          <Newuser kFormatter={kFormatter} warning={colors.warning.main} />
          </Col>
          
        </Row>
        </Col> */}
        
      
        {/* Stats Card */}
      </Row>
      {/* <Row>
        <Col lg="6" sm="12">
          <CardBrowserState colors={colors} trackBgColor={trackBgColor} />
        </Col>
        <Col lg="3" xs="6">
          <StatsVertical
            icon={<Smartphone size={21} />}
            color="info"
            stats={userSignUpCountPlatform.mobile_signup_count}
            statTitle="Signup via Mobile"
          />
        </Col>
        <Col lg="3" sm="6">
          <StatsVertical
            icon={<Mail size={21} />}
            color="primary"
            stats={userSignUpCountPlatform.email_signup_count}
            statTitle="Signup via Email"
          />
        </Col>
      </Row> */}

      {/* <Row>
        <Col lg="4">
          <GoalOverview />
        </Col>
       </Row> */}

      
    </Fragment>
  )
}

export default StatisticsCards
