// ** Third Party Components
import PropTypes from 'prop-types'
import { Card, CardBody } from 'reactstrap'

const Uninstall = ({ icon, color, stats, statTitle, className }) => {
  return (
    <Card className="bg-danger card h-100">
      <CardBody className={className}>
        <div
          className={`avatar p-50 m-0 mb-1 ${
            color ? `bg-light-white` : 'bg-light-primary'
          }`}
        >
          <div className="avatar-content ">{icon}</div>
        </div>
        <h2 className="font-weight-bolder">{stats}</h2>
        <p className="card-text line-ellipsis font-weight-bolder text-white">{statTitle}</p>
      </CardBody>
    </Card>
  )
}

export default Uninstall

// ** PropTypes
Uninstall.propTypes = {
  icon: PropTypes.element.isRequired,
  color: PropTypes.string.isRequired,
  statTitle: PropTypes.string.isRequired,
  className: PropTypes.string
}
